import { wordSolver } from './components/word-solver.component.js';

const resultDiv = document.getElementById('results');
const messagesDiv = document.getElementById('messages');
const lettersInput = document.getElementById('input-letters');

document.getElementById('solve-button').addEventListener('click', () => {
    clearResult();
    clearMessage();
    const letters = getLetters();
    wordSolver({ letters, minLength: 3, maxLength: letters.length })
        .then(result => {
            order(result);
            setResult(result.map(w => `<div class="result-item">${w}</div>`).join(''));
        })
        .catch(error => {
            console.error("Falha ao buscar pelas letras!", error);
            setMessage("<div>Falhou!</div>");
        })
        .finally(() => {
            setMessage("<div>Terminou!</div>");
        });
});

function clearResult() {
    resultDiv.innerHTML = '';
}

function setResult(result) {
    resultDiv.innerHTML += result;
}

function clearMessage() {
    messagesDiv.innerHTML = '';
}

function setMessage(message) {
    messagesDiv.innerHTML = message;
}

function getLetters() {
    return lettersInput.value;
}

function order(words) {
    words.sort((a, b) => {
        if (a.length !== b.length) {
            return a.length - b.length;
        }
        return a.localeCompare(b);
    });
}